nav .hamburger {
  display: none;
  font-size: 2rem;
  cursor: pointer;
  color: white;
}

/* Media Query for Smaller Screens */
@media screen and (max-width: 768px) {
  nav .nav-links {
    display: none;
    flex-direction: column;
    position: absolute;
    top: 4rem;
    right: 0;
    background-color: #333;
    width: 100%;
    padding: 1rem;
  }

  nav .nav-links.open {
    display: flex;
    margin-top: 3vh;
    
  }

  nav .nav-links.open ul {
    display: flex;
    flex-direction: column;
    /* margin-top: 10vh; */
    gap: 2rem;
    align-items: flex-start;
    text-decoration: none;
    color: #fff;
  }
  /* .pages{
    position: static;
  } */

  nav .hamburger {
    display: block;
  }

  .pages {
  
    position: relative;
    text-decoration: none;
    font-family: "Poppins", sans-serif;
    color: #a0a0a0;
    font-size: 25px;
    letter-spacing: 0.5px;
    padding: 0 10px;
  }
  .pages:before,
  .pages:after {
    content: "";
    position: absolute;
    height: 3px;
    width: 0;
    background-color: #18f08b;
    transition: 0.5s;
    
  }
  .pages:after {
    left: 0;
    bottom: -10px;
  }
  .pages:before {
    right: 0;
    top: -10px;
  }
  .pages:hover {
    color: #ffffff;
    font-size: 25px;
  }
  .pages:hover:after,
  .pages:hover:before {
    width: 100%;
  }
}

/* Animation for the intro text */
.intro-animation {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  animation: fadeInOut 2s ease-in-out forwards; /* Fades in and out over 2 seconds */
}

.intro-animation h1 {
  font-size: 50px;
  color: black;
  font-family: 'Arial', sans-serif;
  text-transform: uppercase;
  letter-spacing: 5px;
}

/* Keyframes for fading in and out */
@keyframes fadeInOut {
  0% {
    opacity: 0;
    transform: translate(-50%, -50%) scale(0.5);
  }
  50% {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
  }
  100% {
    opacity: 0;
    transform: translate(-50%, -50%) scale(1.2);
  }
}

