.waviy {
  position: relative;
  -webkit-box-reflect: below -20px linear-gradient(transparent, rgba(0,0,0,.2));
  font-size: 30px;
}
.waviy span {
  font-family: 'Alfa Slab One', cursive;
  position: relative;
  display: inline-block;
  color: #fff;
  text-transform: uppercase;
  animation: waviy 8s ease-in-out  infinite;
  animation-delay: calc(.1s * var(--i));
  
}
@keyframes waviy {
  0%, 12.5%, 100% {
    transform: translateY(0); /* Animation starts and ends with letters in place */
  }
  6.25% {
    transform: translateY(-20px); /* Peak of the animation */
  }
}
